<template>
  <section id="screen-2" class="screen">

    <router-link v-for="item in menuItem" :key="item.h1" :to="item.src"
                 class="menu-item" >
      <div class="title-wrap">
        <span class="title">{{ item.h1 }}</span>
      </div>
      <div class="menu-gradient"></div>
                <img id="img-poster" :src="require(`@/assets/video/posters/${item.video}.jpg`)" alt="poster" :class="{'offlinePoster' : !networkStatus}">
                <video v-if="networkStatus" :id="item.id" class="menu-video" muted
                       playsinline preload="auto">
                  <source :src="`https://evoxeed.github.io/dist_video/media/${item.video}.mp4`" type="video/mp4">
                </video>
