  </section>
</template>

<script>
import {getMenuItem} from "@/data";
import {section} from '@/functions/menu'
import {VueOfflineMixin} from 'vue-offline'


export default {
  name: "VMenu.vue",
  mixins: [VueOfflineMixin],
  data: function () {
    return {
      menuItem: []
    }
  },
  methods: {
    toImg(id, video) {
      this.$refs[id][0].setAttribute("src", require(`@/assets/video/posters/${video}.jpg`))
    },
    toGif(id, video) {
      this.$refs[id][0].setAttribute("src", require(`@/assets/video/${video}.gif`))
    },

  },
  mounted() {
    this.menuItem = getMenuItem()
  },

  updated() {
    section()
  },

  computed: {
    networkStatus() {
      return !!this.isOnline
    }
  }
}
</script>